$icon--dimension: 2.1875rem;
$btn--dimension: 2.375rem;

.search-bar {
  width: 100%;
  height: 3.375rem;
  padding: 5px 1.25rem;
  display: flex;
  justify-content: center;
  position: relative;
}

.search-input {
  min-width: 45%;
  min-height: 35.5px;
  border-radius: 20px;
  border: 0;
  padding: 2px 1.875rem;
  font-size: 1.5rem;
  transition: min-width 0.5s ease-in-out;
}

.search-input:focus {
  min-width: 55%;
}

.search-icon {
  width: $icon--dimension;
  height: $icon--dimension;
}

.search-button {
  right: 50px;
  top: 3px;
  position: relative;
  width: $btn--dimension;
  height: $btn--dimension;
  background: none;
}

.price-selector {
  border-radius: 10px;
  border: 0;
  padding: 2px 4px;
  font-size: 1.2rem;
  width: 4.1rem;
  text-align: center;
}

.price-selector-container {
  min-width: 9.4rem;
  display: flex;
  justify-content: space-between;
}
