$size: 90px;

#spinner {
  display: inline-block;
  width: $size;
  height: $size;
  margin: auto;
  border: 10px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: #fff;
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
